<template>
    <div class="read-artlist">
        <div class="reader-header">
            <img src="http://images.homeinns.com/image/web/brand/home/puyin.jpg" />
        </div>
        <div class="title-wrapper">
            <div class="cur-category">
                <span class="iconfont icon-local"></span>
                文章阅览
            </div>
            <ul>
                <li class="item" @click="back">
                    返回
                </li>
            </ul>
            <div class="artlist">
                <li class="art-item-title">文章列表</li>
                <div class="scroll-wrapper" ref="scrollwrapper">
                    <ul>
                        <li class="art-item" :class="{'art-item-active': currentArticleIndex == index}"
                            v-for="(article, index) in list" :key="index"
                        @click="changeArt(index)">
                            {{article.title}}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="art-wrapper">
            <div class="article">
                <h1>{{ article.title }}</h1>
                <div class="cont" v-html="article.content">

                </div>
                <div class="art-time">
                    {{ article.create_time | date }}
                </div>
            </div>
            <div class="art-bottom">
                <p class="pre" @click="preOrNext(true)" :title='preTitle'>上一篇：{{ preTitle }}</p>
                <p>{{article.title}}</p>
                <p class="next" @click="preOrNext(false)" :title='nextTitle'>下一篇：{{ nextTitle }}</p>
            </div>
        </div>
        <div class="sec-footer">
            <Footer />
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
import Footer from '_c/footer'
import { articleMixin } from 'common/js/mixin'
export default {
    mixins: [articleMixin],
    data () {
        return {
            article: {},
            list: []
        }
    },
    mounted() {
        this.article = this.currentArticle
        this.list = this.articleList
        this.initScroll()
    },
    methods: {
        initScroll () {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.scrollwrapper, {
                    click: true
                })
            } else {
                this.scroll.refresh()
            }
        },
        preOrNext (tag) {
            if (tag) {
                if (this.list[this.currentArticleIndex - 1]) {
                    this.set_articleIndex(this.currentArticleIndex - 1)
                }
            } else {
                if (this.list[this.currentArticleIndex + 1]) {
                    this.set_articleIndex(this.currentArticleIndex + 1)
                }
            }
        },
        changeArt (index) {
            this.set_articleIndex(index)
        },
        handleArticleListChange () {
            this.list = this.articleList
        },
        handleCurrentArticle () {
            this.article = this.currentArticle
        },
        back () {
            this.$router.back()
        }
    },
    computed: {
        preTitle () {
            return this.list[this.currentArticleIndex - 1] 
                ? this.list[this.currentArticleIndex - 1].title : '无'
        },
        nextTitle () {
            return this.list[this.currentArticleIndex + 1] 
                ? this.list[this.currentArticleIndex + 1].title : '无'
        }
    },
    components: {
        Footer
    },
    filters: {
        date (t) {
            t = t ? new Date(Number(t)) : new Date(1545977032592)
            let reg = /\//g
            return t.toLocaleDateString().replace(reg, '-')
        }
    }
}
</script>
<style lang="less" scoped>
.read-artlist{
    .reader-header{
        height: 300px;
        overflow: hidden;
    }
    .title-wrapper{
        width: 1200px;
        margin: 0 auto;
        position: relative;
        .cur-category{
            text-align: right;
            font-size: 25px;
            font-weight: 700;
            color: #e0c073;
            padding: 10px;
            span{
                font-weight: 100;
                font-size: 22px;
                margin-right: 10px;
            }
        }
        ul{
            border-bottom: 1px solid #ccc;
            text-align: left;
            .item{
                margin-right: 5px;
                line-height: 50px;
                padding: 0 42px;
                display: inline-block;
                cursor: pointer;
                transition: all .5s;
                color: white;
                background-color: #e0c073;
            }
        }
        .artlist{
            position: absolute;
            right: 0;
            box-shadow: 0 0 2px #ccc;
            height: 500px;
            width: 200px;
            transform: translateX(110%);
            overflow: hidden;
            .art-item-title{
                text-align: center;
                padding: 8px 0;
                background-color: #e0c073;
                color: white;
            }
            .scroll-wrapper{
                max-height: 460px;
                overflow: hidden;
                ul{
                    border: none;
                    .art-item{
                        padding: 6px 10px;
                        cursor: pointer;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        border-bottom: 1px solid rgba(223, 190, 112, 0.3);
                    }
                    .art-item-active{
                        color: #e0c073;
                    }
                }
            }
        }
    }
    .art-wrapper{
        width: 1200px;
        margin: 0 auto;
        min-height: 600px;
        overflow: hidden;
        .article{
            h1{
                margin: 20px 0;
                color: #e0c073;
                font-weight: normal;
            }
            .empty{
                padding: 1px 0 ;
            }
            .cont{
                padding: 10px 30px 50px 30px;
                text-align: left;
                // text-indent: 2em;
                line-height: 45px;
                font-size: 17px;
            }
            .art-time{
                padding: 20px;
                text-align: right;
            }
        }
    }
    .art-bottom{
        border-top: 1px solid;
        padding: 15px 0;
        display: flex;
        justify-content: space-between;
        color: #e0c073;
        p{
            flex: 0 0 230px;
            cursor: pointer;
            // padding: 0 50px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
    .sec-footer{
        background-color: #e0c073;
    }
}
</style>





